.App-header {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.App-header a {
    text-transform: uppercase;
    font-size: calc(10px + 1.5vmin);
    padding: 2.5vw 2vw;
    text-align: center;
  }

.menu {
  background-color: #ffffff;
  border: 1px solid black;
  border-top: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  align-items: center;
  justify-items: stretch;
  border-radius: 0 0 20px 20px;
  height:max-content;
  overflow: hidden;
}

.menu a:hover {
  background-color: rgb(207, 185, 176);
}

.menu > *:not(:last-child) {
  border-right: 1px solid black;
}

.mark {
    display: flex;
    width:fit-content;
    background-color: rgb(138, 20, 20);
    width: 200px;
    color: white;
}



 



